.MenuComp {
  border: 1px solid #ffffff;
  box-shadow: 3px 3px #888888;
  display: flex;
  flex-direction: column;
  justify-content: left;

  div {
    margin-bottom: 1vh;
    margin-top: 1vh;
    padding: 1vh;
    text-align: left;
  }

  p {
    margin-bottom: 3px;
    margin-top: 3px;
  }

  button {
    color: inherit;
    font: inherit;
    width: 100%;
  }

  input:not([type='checkbox']) {
    display: block;
    width: 100%;
  }
}
